import React, { useEffect, useState } from "react"
import "./Status.css"
import classNames from "classnames"
import {
  getPoints,
  getCars,
  getCities,
  getOrderId,
} from "../../../store/order-selectors"
import { connect } from "react-redux"
import { submitOrder } from "../../../store/order-reducer"
import { withRouter } from "react-router-dom"
import { compose } from "redux"
import { Button, LinkButton } from "../../common/Button/Button"

const Status = ({
  isFinished,
  step,
  setStep,
  stepDisabled,
  setStepDisabled,
  formData,
  cities,
  points,
  cars,
  submitOrder,
  orderId,
  history,
}) => {
  const [isModalVisible, setIsModalVisible] = useState(false)
  const statusBtnClasses = classNames("button status__price-btn", {
    "button--cancel": isFinished === true,
  })

  useEffect(() => {
    if (orderId) {
      history.push(`/order/finished/${orderId}`)
    }
  }, [history, orderId])

  const onModalConfirm = () => {
    if (isFinished) {
      setStep(1)
      setIsModalVisible(false)
    } else {
      submitForm()
    }
  }

  const onButtonClick = () => {
    if (step === 4 || isFinished) {
      setIsModalVisible(!isModalVisible)
    } else {
      const nextStep = step + 1
      setStepDisabled({
        ...stepDisabled,
        [nextStep]: false,
      })
      setStep(nextStep)
    }
  }

  const isPlaceValid = () =>
    points.find((point) => point.address === formData.locationPoint)

  const isButtonDisabled = () => {
    if (isFinished) {
      return false
    }
    if (!isPlaceValid()) {
      return true
    }
    if (step === 2 && formData.model === "") {
      return true
    }
    if (step === 3 && deltaDays === 0) {
      return true
    }
    return false
  }

  const deltaTime = Math.abs(formData.dateTo - formData.dateFrom)
  const deltaMinutes =
    formData.dateTo && formData.dateFrom !== ""
      ? Math.ceil(deltaTime / (1000 * 60))
      : 0
  const deltaHours =
    formData.dateTo && formData.dateFrom !== ""
      ? Math.ceil(deltaTime / (1000 * 60 * 60))
      : 0
  const deltaDays =
    formData.dateTo !== "" && formData.dateFrom !== ""
      ? Math.ceil(deltaTime / (1000 * 60 * 60 * 24))
      : 0

  const modelData = cars.find((car) => car.name === formData.model)

  const getPrice = () => {
    let price = 0
    if (formData.model !== "") {
      if (deltaMinutes === 0) {
        let priceMin =
          formData.rate === "minute"
            ? modelData.priceMin * 2
            : modelData.priceMin
        const priceMax = modelData.priceMax
        if (formData.isFullTank) {
          priceMin += 500
        }
        if (formData.isNeedChildChair) {
          priceMin += 200
        }
        if (formData.isRightWheel) {
          priceMin += 1600
        }
        price = `${priceMin} - ${priceMax}`
      } else {
        price = Math.round(
          formData.rate === "minute"
            ? (modelData.priceMin / (60 * 24)) * deltaMinutes * 1.5
            : modelData.priceMin * deltaDays
        )
        if (formData.isFullTank) {
          price += 500
        }
        if (formData.isNeedChildChair) {
          price += 200
        }
        if (formData.isRightWheel) {
          price += 1600
        }
      }
    }
    return `${price} ‚ÇΩ`
  }

  const submitForm = () => {
    const { color, isFullTank, isNeedChildChair, isRightWheel } = formData
    const cityId = cities.find((city) => city.name === formData.locationCity).id
    const pointId = points.find(
      (point) => point.address === formData.locationPoint
    ).id
    const carId = modelData.id
    const dateFrom = formData.dateFrom.getTime()
    const dateTo = formData.dateTo.getTime()
    const rateId =
      formData.rate === "day"
        ? "5e26a0e2099b810b946c5d86"
        : "5e26a0d2099b810b946c5d85"
    const price = getPrice()

    submitOrder(
      cityId,
      pointId,
      carId,
      color,
      dateFrom,
      dateTo,
      rateId,
      price,
      isFullTank,
      isNeedChildChair,
      isRightWheel,
      setIsModalVisible
    )
  }

  const buttonText = () => {
    switch (step) {
      case 1:
        return "–í—ã–±—Ä–∞—Ç—å –º–æ–¥–µ–ª—å"
      case 2:
        return "–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–æ"
      case 3:
        return "–ò—Ç–æ–≥–æ"
      case 4:
        return isFinished ? "–û—Ç–º–µ–Ω–∏—Ç—å" : "–ó–∞–∫–∞–∑–∞—Ç—å"
      default:
        return "–í—ã–±—Ä–∞—Ç—å –º–æ–¥–µ–ª—å"
    }
  }

  return (
    <div className="status">
      {isModalVisible && (
        <div className="modal">
          <div className="modal__overlay" />
          <div className="modal__container">
            <div className="modal__title">
              {isFinished ? "–û—Ç–º–µ–Ω–∏—Ç—å –∑–∞–∫–∞–∑" : "–ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç—å –∑–∞–∫–∞–∑"}
            </div>
            <div className="modal__buttons">
              <LinkButton
                to={isFinished ? "/order" : false}
                onClick={() => onModalConfirm()}
              >
                –ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç—å
              </LinkButton>
              <Button
                onClick={() => setIsModalVisible(false)}
                additionalStyles="button--cancel"
              >
                –í–µ—Ä–Ω—É—Ç—å—Å—è
              </Button>
            </div>
          </div>
        </div>
      )}
      <div className="status__header">–í–∞—à –∑–∞–∫–∞–∑:</div>
      {formData.locationPoint !== "" && formData.locationCity !== "" && (
        <div className="status__item">
          <div className="status__item-title">–ü—É–Ω–∫—Ç –≤—ã–¥–∞—á–∏</div>
          <div className="status__item-dash" />
          <div className="status__item-value">
            {formData.locationCity}, {formData.locationPoint}
          </div>
        </div>
      )}
      {formData.model !== "" && (
        <div className="status__item">
          <div className="status__item-title">–ú–æ–¥–µ–ª—å</div>
          <div className="status__item-dash" />
          <div className="status__item-value">{formData.model}</div>
        </div>
      )}
      {!stepDisabled[3] && (
        <div className="status__item">
          <div className="status__item-title">–¶–≤–µ—Ç</div>
          <div className="status__item-dash" />
          <div className="status__item-value">{formData.color}</div>
        </div>
      )}
      {!stepDisabled[3] && formData.dateFrom && formData.dateTo && (
        <div className="status__item">
          <div className="status__item-title">–î–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç—å –∞—Ä–µ–Ω–¥—ã</div>
          <div className="status__item-dash" />
          <div className="status__item-value">{`${Math.floor(
            deltaHours / 24
          )}–¥ ${deltaHours % 24}—á`}</div>
        </div>
      )}
      {!stepDisabled[3] && (
        <div className="status__item">
          <div className="status__item-title">–¢–∞—Ä–∏—Ñ</div>
          <div className="status__item-dash" />
          <div className="status__item-value">
            {formData.rate === "day" ? "–ù–∞ —Å—É—Ç–∫–∏" : "–ü–æ–º–∏–Ω—É—Ç–Ω–æ"}
          </div>
        </div>
      )}
      {!stepDisabled[3] && formData.isFullTank && (
        <div className="status__item">
          <div className="status__item-title">–ü–æ–ª–Ω—ã–π –±–∞–∫</div>
          <div className="status__item-dash" />
          <div className="status__item-value">–î–∞</div>
        </div>
      )}
      {!stepDisabled[3] && formData.isNeedChildChair && (
        <div className="status__item">
          <div className="status__item-title">–î–µ—Ç—Å–∫–æ–µ –∫—Ä–µ—Å–ª–æ</div>
          <div className="status__item-dash" />
          <div className="status__item-value">–î–∞</div>
        </div>
      )}
      {!stepDisabled[3] && formData.isRightWheel && (
        <div className="status__item">
          <div className="status__item-title">–ü—Ä–∞–≤—ã–π —Ä—É–ª—å</div>
          <div className="status__item-dash" />
          <div className="status__item-value">–î–∞</div>
        </div>
      )}
      <div className="status__price">
        <span className="status__price-header">–¶–µ–Ω–∞: </span>
        <span className="status__price-digits">{getPrice()}</span>
      </div>

      <Button
        additionalStyles={statusBtnClasses}
        onClick={onButtonClick}
        disabled={isButtonDisabled()}
      >
        {buttonText()}
      </Button>
    </div>
  )
}

const mapStateToProps = (state) => ({
  points: getPoints(state),
  cars: getCars(state),
  cities: getCities(state),
  orderId: getOrderId(state),
})

export default compose(
  connect(mapStateToProps, { submitOrder }),
  withRouter
)(Status)
